<template>
  <div>
      <b-container class="bv-example-row">
    <b-row class="fila2">
        <b-col cols="1" md="2">&nbsp;</b-col>
        <b-col cols="10" md="8">
          <h2>Formulario</h2>
            <b-form @submit="onSubmit" @reset="onReset" v-if="show" class="bform"   >
      <b-form-group
        id="input-group-1"
        label="Correo electrónico:"
        label-for="input-1"
      >
        <b-form-input
          id="input-1"
          v-model="form.email"
          type="email"
          placeholder="nombre@tesito.com"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Nombre:" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="form.name"
          placeholder="Nombre Apellido1 Apellido2"
          required
        ></b-form-input>
      </b-form-group>

     <b-form-group id="input-group-4" label="Tema:" label-for="input-4">
        <b-form-select
          id="input-4"
          v-model="form.food"
          :options="foods"
          required
        ></b-form-select>
      </b-form-group>

      <b-form-group id="input-group-3" label="¿Qué necesitas?" label-for="textarea">
        <b-form-textarea
      id="textarea"
      v-model="text"
      placeholder="Escribe aquí..."
      rows="3"
      max-rows="6"
    ></b-form-textarea>
      </b-form-group>

      <b-form-group id="input-group-5" v-slot="{ ariaDescribedby }">
        <b-form-checkbox-group
          v-model="form.checked"
          id="checkboxes-4"
          :aria-describedby="ariaDescribedby"
        >
          <b-form-checkbox value="me">Quiero suscribirme a la newsletter</b-form-checkbox>
        </b-form-checkbox-group>
      </b-form-group>

      <b-button type="submit" variant="dark">Enviar</b-button>
      <b-button type="reset" variant="danger">Borrar</b-button>
    </b-form>
        </b-col>
        <b-col cols="1" md="2">&nbsp;</b-col>

    </b-row>
  </b-container>
