</template>

<script>
  export default {
    data() {
      return {
        form: {
          email: '',
          name: '',
          food: null,
          checked: []
        },
        foods: [{ text: 'Seleccionar', value: null }, 'Pedidos', 'Opiniones', 'Trabajo', 'Otros'],
        show: true
      }
    },
    methods: {
      onSubmit(event) {
        event.preventDefault()
        alert("¡Tu pedido se ha enviado correctamente!")
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        this.form.text= ''
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
  }
</script>

